import './App.css';
import pdi__light from './assets/svg/pdi__light.svg'
import pdi__dark from './assets/svg/pdi__dark.svg'


import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link
} from "react-router-dom";

export default function App() {
  return (
    <Router>
      <div className="wrapper__switch">
        <div className="wrapper__pillar">
          <div className="pillar">
            <img className="pillar__logo" src={pdi__light} alt="pillar-logo" />
          </div>
        </div>
        <nav>
          <ul>
            <li>
              <Link to="/">Start</Link>
            </li>
            <li>
              <Link to="/question">Question</Link>
            </li>
            <li>
              <Link to="/result">Result</Link>
            </li>
          </ul>
        </nav>

        <div className="wrapper__background">
          <Switch>
            <Route exact path="/">
              <Start />
            </Route>
            <Route path="/question">
              <Question />
            </Route>
            <Route path="/result">
              <Result />
            </Route>
          </Switch>
        </div>

        <footer>
          <img className="footer__logo" src={pdi__dark} alt="footer-logo" />
        </footer>
      </div>
    </Router>
  );
}

function Start() {
  return <h2 style={{height:"100vh"}}>Start</h2>;
}

function Question() {
  return <h2>Question</h2>;
}

function Result() {
  return <h2>Result</h2>;
}